.card {
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__description {
  color: #060b35;
  width: 166px;
  height: 36px;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  height: 14px;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card__reviews-reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__stars {
  justify-content: space-between;
  display: flex;
}

.card__stars-stars {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.card__stars-stars:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__stars-stars:first-child {
  margin-left: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
}

.page {
  margin: 8px;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}
